import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import loginSide from "../assets/images/loginSide.png"; // Image for side section
import logo from "../assets/images/logo.svg"; // Logo image

const FormFields = ({ label, type, id, placeholder }) => (
  <div className="d-flex flex-column mb-3">
    <label htmlFor={id}>{label}</label>
    <input type={type} id={id} placeholder={placeholder} required />
  </div>
);
// Logo Section Component (Left Side)
const LoginSideImage = () => (
  <div className="LoginFormWrp__left">
    <img src={loginSide} alt="Login Side" />
    <div className="LoginFormWrp__logo">
      <img src={logo} alt="logo" />
    </div>
  </div>
);

// Main Login Component
const LearnerLogin = () => (
  <Container>
    <Row>
      <Col lg={12}>
        <div className="LoginFormWrp d-flex">
          <LoginSideImage />
          <div className="LoginFormWrp__right">
            <h2>Forget Password</h2>
            <form className="LoginFormWrp__form d-flex flex-column">
              <FormFields
                label="* Your Email"
                type="email"
                id="email"
                placeholder="Enter your email..."
              />
              <div className="d-flex flex-column gap-3 LoginFormWrp__btnWrp">
                <button type="submit" className="btn btn-white has-hover-blue">
                  Submit &#62;
                </button>
              </div>
            </form>
          </div>
        </div>
      </Col>
    </Row>
  </Container>
);

export default LearnerLogin;
